import React from "react";

const ProductList = props => {
  return (
    <div style={{ margin: "0px 15px 0px 15px" }}>
      <h2>Gi hng ca bn !</h2>
      <section className="section">
        <div className="table-responsive">
          <table className="table product-table">
            <thead>
              <tr style={{ fontSize: "25px", color: "#000000" }}>
                <th />
                <th style={{ color: "#000000" }}>Sn Phm</th>
                <th style={{ color: "#000000" }}>Gi</th>
                <th style={{ color: "#000000" }}>S Lng</th>
                <th style={{ color: "#000000" }}>Tng Cng</th>
                <th />
              </tr>
            </thead>
            <tbody>{props.children}</tbody>
            <tr>
              <td />
              <td />
              <td />
              <td />
              <td
                style={{
                  fontWeight: "bold",
                  color: "#c10017"
                }}
              >
                <span style={{ color: "#000000" }}>Thnh tin : </span>{" "}
                <span id="total">
                  {new Intl.NumberFormat().format(props.total()) + ""}
                </span>
              </td>
            </tr>
          </table>
        </div>
      </section>
    </div>
  );
};
const getTotalPrice = props => {
  return props.total();
};
export default ProductList;



