import React, {Component} from "react";
import {withRouter} from "react-router-dom";
import "../../../assets/css/adminmanager.css";

class Menu extends Component {
    logOutEvent = () => {
        localStorage.removeItem("token");
        localStorage.removeItem("role");
        localStorage.removeItem("username");
        this.props.history.push("/login");
    };

    render() {
        const styleLogout = {float: "right", "margin-right": "30px"};
        return (
            <div class="admin-menu">
                <div className="navbar navbar-default">
                    <a className="navbar-brand">Admin Manager</a>
                    <ul className="menu nav navbar-nav">
                        <li className="">
                            <a
                                onClick={() => {
                                    this.props.history.push("/adminmanager/user");
                                }}
                            >
                                Qun L Ti Khon
                            </a>
                        </li>
                        <li className="">
                            <a
                                onClick={() => {
                                    this.props.history.push("/adminmanager/productview/");
                                }}
                            >
                                Qun L Sn Phm
                            </a>
                        </li>
                    </ul>
                    <ul className="nav navbar-nav" style={styleLogout}>
                        <li>
                            <a href="#" onClick={this.logOutEvent}>
                                Log Out
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        );
    }
}

export default withRouter(Menu);



